import {
	Nav,
	NavItem,
	TabPane,
	NavLink,
	TabContent,
} from "reactstrap";
import classNames       from "classnames";
import PerfectScrollbar from "react-perfect-scrollbar";

//Our Importaions
import { FilesTab, RecordTab, SignatureTab } from "../NavTabs";
import "./SideBarModal.scss";

const dropeZoneSize ={ width : 300, height : 100 };

const SideBarModal = ({
	hookForm,
	delegations : {
		ac,
		isRequire,
		toggleTab,
		activeTab,
		handleOpen,
		shfOptions,
		handleSelectChange,
	},
}) => {
	return (
		<div
			id="SideBarModal"
			className={classNames("", {
				"wrapper-sidebar" : handleOpen(),
			})}
		>
			<Nav tabs fill className="nav-header">
				<NavCustomItem
					title="Registro"
					activeTab={activeTab === "1"}
					toggleTab={() => toggleTab("1")}
					errors={!!hookForm.formState.errors?.record}
				/>
				<NavCustomItem
					title="Documentos"
					activeTab={activeTab === "2"}
					toggleTab={() => toggleTab("2")}
					errors={hookForm.formState.errors?.files}
				/>
				<NavCustomItem
					title="Firmas"
					activeTab={activeTab === "3"}
					toggleTab={() => toggleTab("3")}
					errors={hookForm.formState.errors?.signature}
				/>
			</Nav>
			<div className="body-sidebar">
				<PerfectScrollbar>
					<div className="wrapper">
						<TabContent className="py-50 pr-2" activeTab={activeTab}>
							<TabPane className="pr-2" tabId="1">
								<RecordTab
									ac={ac}
									hookForm={hookForm}
									shfOptions={shfOptions}
									handleSelectChange={handleSelectChange}
								/>
							</TabPane>
							<TabPane tabId="2">
								<FilesTab
									hookForm={hookForm}
									dropeZoneSize={dropeZoneSize}
									handleSelectChange={handleSelectChange}
								/>
							</TabPane>
							<TabPane tabId="3">
								<SignatureTab
									hookForm={hookForm}
									isRequire={isRequire}
									dropeZoneSize={dropeZoneSize}
									handleSelectChange={handleSelectChange}
								/>
							</TabPane>
						</TabContent>
					</div>
				</PerfectScrollbar>
			</div>
		</div>
	);
};

const NavCustomItem = ({
	title,
	errors,
	toggleTab,
	activeTab,
}) => (
	<NavItem>
		<NavLink
			active={activeTab}
			onClick={toggleTab}
			className={classNames("", {
				"invalid" : errors,
			})}
		>
			{title}
		</NavLink>
	</NavItem>
);

export default SideBarModal;
