
//Our Imports
import FormInputGroup from "Components/Global/FormInputGroup";
import DropeZoneFile  from "Components/Global/DropeZoneFile";
import { Password }   from "Resources/icons";

const SignarutrTab = ({
	hookForm,
	isRequire,
	dropeZoneSize,
	handleSelectChange,
}) => {
	return (
		<>
			<DropeZoneFile
				label=".key"
				customStylesText="dropezone-text"
				defaultFile={hookForm.getValues("signature.signature_file")}
				errors={hookForm.formState.errors?.signature?.signature_file}
				setValue={(val)  => handleSelectChange("signature.signature_file", val)}
				{...dropeZoneSize}
			/>
			<DropeZoneFile
				label=".cer"
				customStylesText="dropezone-text"
				defaultFile={hookForm.getValues("signature.cerf_file")}
				errors={hookForm.formState.errors?.signature?.cerf_file}
				setValue={(val)  => handleSelectChange("signature.cerf_file", val)}
				{...dropeZoneSize}
			/>
			<FormInputGroup
				type="password"
				name="signature.signing_password"
				label="Contraseña"
				icon={<Password size={15} />}
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{
					required : {
						value   : isRequire(),
						message : "Este campo no puede estar vacío.",
					},
				}}
			/>
		</>
	);
};


export default SignarutrTab;
