import DropeZoneFile from "Components/Global/DropeZoneFile";

const FilesTab = ({
	hookForm,
	dropeZoneSize,
	handleSelectChange,
}) => {
	return (
		<>
			<DropeZoneFile
				customStylesText="dropezone-text"
				label="Constancia de Situación Fiscal"
				defaultFile={hookForm.getValues("files.fiscal_situtation_file")}
				errors={hookForm.formState.errors?.files?.fiscal_situtation_file}
				setValue={(val)  => handleSelectChange("files.fiscal_situtation_file", val)}
				{...dropeZoneSize}
			/>
			<DropeZoneFile
				label="CURP"
				customStylesText="dropezone-text"
				defaultFile={hookForm.getValues("files.curp_file")}
				errors={hookForm.formState.errors?.files?.curp_file}
				setValue={(val)  => handleSelectChange("files.curp_file", val)}
				{...dropeZoneSize}
			/>
			<DropeZoneFile
				label="Cédula Profesional"
				customStylesText="dropezone-text"
				defaultFile={hookForm.getValues("files.profetional_id_file")}
				errors={hookForm.formState.errors?.files?.profetional_id_file}
				setValue={(val)  => handleSelectChange("files.profetional_id_file", val)}
				{...dropeZoneSize}
			/>
			<DropeZoneFile
				label="Cédula de Especialización"
				customStylesText="dropezone-text"
				defaultFile={hookForm.getValues("files.specialization_id_file")}
				errors={hookForm.formState.errors?.files?.specialization_id_file}
				setValue={(val)  => handleSelectChange("files.specialization_id_file", val)}
				{...dropeZoneSize}
			/>
			<DropeZoneFile
				label="Comprobante de Domicilio"
				customStylesText="dropezone-text"
				defaultFile={hookForm.getValues("files.address_file")}
				errors={hookForm.formState.errors?.files?.address_file}
				setValue={(val)  => handleSelectChange("files.address_file", val)}
				{...dropeZoneSize}
			/>
			<DropeZoneFile
				label="INE"
				customStylesText="dropezone-text"
				defaultFile={hookForm.getValues("files.ine_file")}
				errors={hookForm.formState.errors?.files?.ine_file}
				setValue={(val)  => handleSelectChange("files.ine_file", val)}
				{...dropeZoneSize}
			/>
			<DropeZoneFile
				label="CV"
				customStylesText="dropezone-text"
				defaultFile={hookForm.getValues("files.cv_file")}
				errors={hookForm.formState.errors?.files?.cv_file}
				setValue={(val)  => handleSelectChange("files.cv_file", val)}
				{...dropeZoneSize}
			/>
			<DropeZoneFile
				maxFiles={15}
				className="mb-4"
				customStylesText="dropezone-text"
				defaultFile={hookForm.getValues("files.other_files")}
				label="Otros Archivos (Máximo 15 archivos)"
				acceptFiles={{ image : true, xlsx : true, docs : true, pdf : true }}
				setValue={(val)  => handleSelectChange("files.other_files", val)}
			/>
		</>
	);
};

export default FilesTab;
