import { Input } from "reactstrap";

//Our Imports
import FormFlatPickrGroup from "Components/Global/FormFlatPickrGroup";
import FormInputGroup     from "Components/Global/FormInputGroup";
import FormSelectGroup    from "Components/Global/FormSelectGroup";

const RecordTab = ({
	ac,
	hookForm,
	shfOptions,
	handleSelectChange,
}) => {
	return (
		<>
			<p className="title">SHF</p>
			<FormInputGroup
				type="number"
				name="record.shf"
				disabled={ac.shf}
				label="Registro ante SHF"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{
					required : {
						value   : !ac.shf,
						message : "Este campo no puede estar vacío.",
					},
					maxLength : {
						value   : !ac.shf ? 7 : undefined,
						message : "Este campo debe de tener máximo 7 carácteres.",
					},
					minLength : {
						value   : !ac.shf ? 7 : undefined,
						message : "Este campo debe de tener mínimo 7 carácteres.",
					},
				}}
			/>
			<FormSelectGroup
				placeholder=""
				className="w-100"
				name="record.type_shf"
				isDisabled={ac.type_shf}
				label="Tipo de Registro ante SHF"
				options={shfOptions}
				value={hookForm.getValues("record.type_shf")}
				onChange={(e)  => handleSelectChange("record.type_shf", e)}
				errors={hookForm.formState.errors}
			/>
			<FormFlatPickrGroup
				name="record.date_expiration"
				disabled={ac.date_expiration}
				label="Vigencia de Registro"
				errors={hookForm.formState.errors}
				value={hookForm.getValues("record.date_expiration")}
				onChange={([date]) => handleSelectChange("record.date_expiration", date)}
			/>

			<hr />
			<p className="title">TDF</p>
			<FormInputGroup
				name="record.key_tdf"
				disabled={ac.key_tdf}
				label="Clave TDF"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{
					required : {
						value   : !ac.key_tdf,
						message : "Este campo no puede estar vacío.",
					},
					maxLength : {
						value   : !ac.key_tdf ? 8 : undefined,
						message : "Este campo debe de tener máximo 8 carácteres.",
					},
					minLength : {
						value   : !ac.key_tdf ? 7 : undefined,
						message : "Este campo debe de tener mínimo 7 carácteres.",
					},
				}}
			/>
			<FormInputGroup
				name="record.key_perit_independent"
				disabled={ac.key_perit_independent}
				label="Clave Perito Independiente"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{
					required : {
						value   : !ac.key_perit_independent,
						message : "Este campo no puede estar vacío.",
					},
					maxLength : {
						value   : !ac.key_perit_independent ? 8 : undefined,
						message : "Este campo debe de tener 10 carácteres.",
					},
					minLength : {
						value   : !ac.key_perit_independent ? 8 : undefined,
						message : "Este campo debe de tener 10 carácteres.",
					},
				}}
			/>
			<CheckList
				name="record.enable_controller_evaluator"
				disabled={ac.enable_controller_evaluator}
				text="Habilitado para ser Controlador y a su vez Valuador de un mismo avalúo (Formato TDF):"
				hookForm={hookForm}
			/>
			<CheckList
				name="record.enable_print_iavsa"
				disabled={ac.enable_print_iavsa}
				text="Habilitado para impresión de formato IAVSA:"
				hookForm={hookForm}
			/>
			<CheckList
				name="record.enable_controller_evaluator_iavsa"
				disabled={ac.enable_controller_evaluator_iavsa}
				text="Habilitado para ser Controlador y a su vez Valuador de un mismo avalúo (Formato IAVSA):"
				hookForm={hookForm}
				customStyles="mb-4"
			/>
		</>
	);
};

const CheckList = ({
	name,
	text,
	hookForm,
	customStyles,
	...rest
}) => (
	<div className={`group ${customStyles}`}>
		<p className="checkbox-text">{text}</p>
		<div className="form-check">
			<Input
				{...rest}
				addon
				type="checkbox"
				className="check-inpt"
				value={hookForm.getValues(name)}
				checked={hookForm.getValues(name)}
				onChange={({ target }) => {
					hookForm.setValue(name,  target.checked);
					hookForm.trigger(name);
				}}
			/>
		</div>
	</div>
);

export default RecordTab;
