import { useState } from "react";
import { connect }  from "react-redux";

//Our Imports
import SideBarModal from "./SideBarModal";

const SideBarModalContainer = ({
	role,
	hookForm,
	...rest
}) => {
	const [activeTab, setActiveTab] = useState("1");

	const toggleTab = tab => {
	  if (activeTab !== tab) {
			setActiveTab(tab);
	  }
	};

	const isRequire = () => {
		return role !== "administrator";
	};

	return (
		<SideBarModal
			hookForm={hookForm}
			delegations={{
				toggleTab,
				activeTab,
				isRequire,
				...rest,
			}}
		/>
	);
};
const mapDispatchToState = ({ _authSlice }) => ({
	role : _authSlice?.user?.role          ?? "",
});

export default connect(mapDispatchToState)(SideBarModalContainer);
