import {
	Modal,
	ModalBody,
	ModalHeader,
	ModalFooter,
	FormGroup,
} from "reactstrap";
import classNames            from "classnames";
import { Mail, Phone, User } from "react-feather";
import { connect }           from "react-redux";
import { Controller }        from "react-hook-form";
import { isValidPhoneNumber} from "react-phone-number-input";
import Input                 from "react-phone-number-input/input";

//Our Importations
import { bindAll }     from "Helpers";
import { Password }    from "Resources/icons";
import UsersSlice      from "Store/Slices/UsersSlice";
import userImg         from "Resources/images/Account.png";
import DropZoneImg     from "Components/Global/DropZoneImg";
import SideBarModal    from "Components/Users/SideBarModal";
import FormInputGroup  from "Components/Global/FormInputGroup";
import FormSelectGroup from "Components/Global/FormSelectGroup";
import SubmitBtn       from "Components/Global/SubmitBtn";
import "./ModalUsers.scss";

const ModalUsers = ({
	usersSliceActions,
	delegations : {
		ac,
		image,
		isOpen,
		loading,
		setImage,
		isEditing,
		hookForm,
		shfOptions,
		rolesOptions,
		statesOptions,
		handleSubmitForm,
		handleSelectChange,
		handleOpenExtraForm,
	},
}) => {

	return (
		<Modal
			size="lg"
			id="ModalUsers"
			isOpen={isOpen}
			className={classNames("modal-dialog-centered", {
				"sidebar-active" : handleOpenExtraForm(),
			})}
		>
			<ModalHeader toggle={() => usersSliceActions.closeCreateUserModal()}>
				<span>Agregar Usuario</span>
			</ModalHeader>
			<SideBarModal
				ac={ac}
				hookForm={hookForm}
				shfOptions={shfOptions}
				handleOpen={handleOpenExtraForm}
				handleSelectChange={handleSelectChange}
			/>
			<form onSubmit={handleSubmitForm}>
				<ModalBody>
					<div className="img-form-wrapper">
						<DropZoneImg
							image={image}
							setImage={setImage}
							defaultImage={userImg}
							 />
						<div>
							<div className="name-row">
								<FormInputGroup
									name="prefix"
									disabled={ac.prefix}
									register={hookForm.register}
									errors={hookForm.formState.errors}
									placeholder="Título"
									options={{
										required : {
											value   : !ac.prefix,
											message : "Este campo no puede estar vacío.",
										},
									}}
								/>
								<FormInputGroup
									name="name"
									disabled={ac.name}
									register={hookForm.register}
									errors={hookForm.formState.errors}
									placeholder="Nombre(s)"
									options={{
										required : {
											value   : !ac.name,
											message : "Este campo no puede estar vacío.",
										},
									}}
								/>
								<FormInputGroup
									name="lastname"
									disabled={ac.lastname}
									register={hookForm.register}
									errors={hookForm.formState.errors}
									placeholder="Apellido Paterno"
									options={{
										required : {
											value   : !ac.lastname,
											message : "Este campo no puede estar vacío.",
										},
									}}
								/>
								<FormInputGroup
									name="maiden_name"
									disabled={ac.maiden_name}
									register={hookForm.register}
									errors={hookForm.formState.errors}
									placeholder="Apellido Materno"
									options={{
										required : {
											value   : !ac.maiden_name,
											message : "Este campo no puede estar vacío.",
										},
									}}
								/>
							</div>
							<div className="contact-data-row">
								<div style={{width : "100%"}}>
									<FormInputGroup
										inputGroupSize="sm"
										name="email"
										disabled={ac.email}
										icon={<Mail size={10} />}
										register={hookForm.register}
										errors={hookForm.formState.errors}
										placeholder="Correo"
										options={{
											required : {
												value   : true,
												message : "Este campo no puede estar vacío.",
											},
											pattern : {
												value   : /\S+@\S+\.\S+/,
												message : "Formato invalido.",
											},
										}}
									/>
								</div>
								<div style={{width : "100%"}}>
									<FormGroup className="form-group-style">
										<div className="input-group-merge input-group input-group-sm">
											<span className={classNames("input-group-text", {
												"invalid" : hookForm.formState.errors?.phone,
											})}>
												<Phone size={10} />
											</span>
											<Controller
												name="phone"
												disabled={ac.phone}
												control={hookForm.control}
												rules={{
													required : true,
													validate : (value) => isValidPhoneNumber(value),
												}}
												render={({ field: { onChange, value }}) => (
													<Input
														international
														id="phone"
														country="MX"
														value={value}
														onChange={onChange}
														placeholder="Número"
														className={classNames("form-control", {
															"invalid" : hookForm.formState.errors?.phone,
														})}
													/>
												)}
											/>
										</div>
										{hookForm.formState.errors.phone && <span className="text-error mb-1">Número Invalido</span>}
									</FormGroup>
								</div>
								<div style={{width : "100%"}}>
									<FormInputGroup
										inputGroupSize="sm"
										name="rfc"
										disabled={ac.rfc}
										register={hookForm.register}
										errors={hookForm.formState.errors}
										placeholder="RFC"
										options={{
											required : {
												value   : !ac.rfc,
												message : "Este campo no puede estar vacío.",
											},
										}}
									/>
								</div>
							</div>
						</div>
					</div>
					<div className="user-row">
						<FormInputGroup
							name="username"
							disabled={ac.username}
							icon={<User size={15} />}
							label="Nombre de Usuario"
							register={hookForm.register}
							errors={hookForm.formState.errors}
							options={{
								required : {
									value   : !ac.username,
									message : "Este campo no puede estar vacío.",
								},
							}}
						/>
						<div style={{ width : "210px"}}>
							<FormSelectGroup
								name="role"
								isDisabled={ac.role}
								label="Rol"
								placeholder=""
								value={hookForm.getValues("role")}
								options={rolesOptions}
								onChange={(e)  => handleSelectChange("role", e)}
								errors={hookForm.formState.errors}
							/>
						</div>
						<FormInputGroup
							type="password"
							name="password"
							disabled={ac.password}
							label="Contraseña"
							icon={<Password size={15} />}
							register={hookForm.register}
							errors={hookForm.formState.errors}
							options={{
								required : {
									value   : !isEditing,
									message : "Este campo no puede estar vacío.",
								},
								minLength : {
									value   : 8,
									message : "La contraseña debe de tener mínimo 8 carácteres.",
								},
							}}
						/>
					</div>
					<h1 className="title-education">Educación</h1>
					<div className="education-section">
						<FormInputGroup
							name="education.career"
							disabled={ac.career}
							label="Carrera"
							register={hookForm.register}
							errors={hookForm.formState.errors}
							options={{
								required : {
									value   : !ac.career,
									message : "Este campo no puede estar vacío.",
								},
							}}
						/>
						<FormInputGroup
							name="education.specialty"
							disabled={ac.specialty}
							label="Especialidad"
							register={hookForm.register}
							errors={hookForm.formState.errors}
							options={{
								required : {
									value   : !ac.specialty,
									message : "Este campo no puede estar vacío.",
								},
							}}
						/>
						<FormInputGroup
							type="number"
							label="Cédula"
							name="education.identification_card"
							disabled={ac.identification_card}
							register={hookForm.register}
							errors={hookForm.formState.errors}
							options={{
								required : {
									value   : !ac.identification_card,
									message : "Este campo no puede estar vacío.",
								},
								minLength : {
									value   : !ac.identification_card ? 7 : undefined,
									message : "Este campo debe de tener mínimo 7 carácteres.",
								},
								maxLength : {
									value   : !ac.identification_card ? 8 : undefined,
									message : "Este campo debe de tener máximo 8 carácteres.",
								},
							}}
						/>
						<FormInputGroup
							type="number"
							label="Cédula de Especialidad"
							name="education.specialty_certificate"
							disabled={ac.specialty_certificate}
							register={hookForm.register}
							errors={hookForm.formState.errors}
							options={{
								required : {
									value   : !ac.specialty_certificate,
									message : "Este campo no puede estar vacío.",
								},
								minLength : {
									value   : !ac.specialty_certificate ? 7 : undefined,
									message : "Este campo debe de tener mínimo 7 carácteres.",
								},
								maxLength : {
									value   : !ac.specialty_certificate ? 8 : undefined,
									message : "Este campo debe de tener máximo 8 carácteres.",
								},
							}}
						/>
					</div>
					<FormSelectGroup
						isMulti
						name="places"
						isDisabled={ac.places}
						label="Estados"
						placeholder=""
						value={hookForm.getValues("places")}
						options={statesOptions}
						onChange={(e)  => handleSelectChange("places", e)}
						errors={hookForm.formState.errors}
					/>
				</ModalBody>
				<ModalFooter>
					<SubmitBtn
						className="btn"
						loading={loading}
						label="Guardar usuario"
						onClick={handleSubmitForm}
					/>
				</ModalFooter>
			</form>
		</Modal>
	);
};

const mapDispatchToProps = bindAll({ UsersSliceActions : UsersSlice.actions });

export default connect(null, mapDispatchToProps)(ModalUsers);
