import { useEffect, useMemo, useState } from "react";
import { connect }                      from "react-redux";
import { useForm }                      from "react-hook-form";
import { UserMinus, UserPlus, UserX }   from "react-feather";

//Our importations
import { bindAll, can, isValidArray } from "Helpers";
import { FilesSlice, UsersSlice }     from "Store/Slices";
import toaster                        from "Components/Global/Toaster";
import GenericSerivices               from "Store/Services/GenericSerivices";
import ModalUsers                     from "./ModalUsers";


const shfOptions = [
	{
		label : "Controlador",
		value : "controller",
	},
	{
		label : "Valuador",
		value : "valuer",
	},

];

const rolesOptions = [
	{
		label : "Administrador",
		value : "administrator",
	},
	{
		label : "Consulta",
		value : "consult",
	},
	{
		label : "Invitado",
		value : "guest",
	},
	{
		label : "Perito",
		value : "perit",
	},
	{
		label : "Administrador  Técnico",
		value : "administrator_technician",
	},
];

const ModalUsersContainer = ({
	role,
	isOpen,
	isEditing,
	userToEdit,
	fileActions,
	usersActions,
	statesOptions,
	isUserDetails,
	filesToRemoved,
	genericSerivices,
	...rest
}) => {
	const sidebarControllerInpts = {
		"record.type_shf"              : (role !== "administrator" ? !isEditing : true),
		"record.date_expiration"       : (role !== "administrator" ? !isEditing : true),
		"files.fiscal_situtation_file" : role !== "administrator",
		"files.curp_file"              : role !== "administrator",
		"files.profetional_id_file"    : role !== "administrator",
		"files.specialization_id_file" : role !== "administrator",
		"files.address_file"           : role !== "administrator",
		"files.ine_file"               : role !== "administrator",
		"signature.signature_file"     : role !== "administrator",
		"signature.cerf_file"          : role !== "administrator",
	};

	const ac = useMemo(() => ({
		prefix                : can(role, isEditing ? "update" : "create", "users", "!prefix"),
		name                  : can(role, isEditing ? "update" : "create", "users", "!name"),
		lastname              : can(role, isEditing ? "update" : "create", "users", "!lastname"),
		maiden_name           : can(role, isEditing ? "update" : "create", "users", "!maiden_name"),
		rfc                   : can(role, isEditing ? "update" : "create", "users", "!rfc"),
		username              : can(role, isEditing ? "update" : "create", "users", "!username"),
		role                  : can(role, isEditing ? "update" : "create", "users", "!role"),
		password              : can(role, isEditing ? "update" : "create", "users", "!password"),
		career                : can(role, isEditing ? "update" : "create", "users", "!career"),
		specialty             : can(role, isEditing ? "update" : "create", "users", "!specialty"),
		identification_card   : can(role, isEditing ? "update" : "create", "users", "!identification_card"),
		specialty_certificate : can(role, isEditing ? "update" : "create", "users", "!specialty_certificate"),
		places                : can(role, isEditing ? "update" : "create", "users", "!places"),

		shf                               : can(role, isEditing ? "update" : "create", "users", "!shf"),
		type_shf                          : can(role, isEditing ? "update" : "create", "users", "!type_shf"),
		date_expiration                   : can(role, isEditing ? "update" : "create", "users", "!date_expiration"),
		key_tdf                           : can(role, isEditing ? "update" : "create", "users", "!key_tdf"),
		key_perit_independent             : can(role, isEditing ? "update" : "create", "users", "!key_perit_independent"),
		enable_controller_evaluator       : can(role, isEditing ? "update" : "create", "users", "!enable_controller_evaluator"),
		enable_print_iavsa                : can(role, isEditing ? "update" : "create", "users", "!enable_print_iavsa"),
		enable_controller_evaluator_iavsa : can(role, isEditing ? "update" : "create", "users", "!enable_controller_evaluator_iavsa"),
	}), [isEditing]);

	const defaultValues = {
		...(userToEdit ?? {}),

		role : rolesOptions.filter(role => role.value === userToEdit?.role)?.[0] ?? undefined,

		places : isValidArray(userToEdit?.places) ?
			userToEdit?.places.map(state => ({ label : state.name, value : state._id}) ) : undefined,

		record : {
			...(userToEdit?.record ?? {}),
			type_shf : shfOptions.filter(role => role.value ===  userToEdit?.record?.type_shf)?.[0] ?? undefined,
		},

		files : (isValidArray(userToEdit?.files) ?
			userToEdit?.files.reduce((prev, file) => {
				return {
					...prev,
					[file.description] :
						file.description === "other_files" ?
							[
								...(isValidArray(prev[file.description])
									? prev[file.description] : []),
								{ ...file, path : file.name },
							]
							:
							{ ...file, path : file.name },
				};
			}, {}) : {}),
	};

	const { reset, unregister, ...hookForm } = useForm({ defaultValues });

	const [image, setImage]     = useState(userToEdit?.image ?? null);
	const [loading, setLoading] = useState(false);

	const handleOpenExtraForm = () => {
		const isOpen = Boolean(hookForm.getValues("role")?.value !== "administrator" && hookForm.getValues("role"));
		return isOpen;
	};

	const handleSelectChange = (key, value) => {
		hookForm.setValue(key, value);
		hookForm.clearErrors(key);
	};

	const handleValidateControllerInpts = () => {
		handleSetError("role");

		handleSetError("places");

		if (!handleOpenExtraForm()) return;

		//Records Tab inpts
		for (const [key, value] of Object.entries(sidebarControllerInpts)) {
			if (value) {
				handleSetError(key);
			}
		}
	};

	const handleSetError = (key, errorMsg = "Este campo no puede estar vacío.") => {
		if (!hookForm.getValues(key)) {
			hookForm.setError(key, {
				type    : "manual",
				message : errorMsg,
			});
		}
	};

	const setFilePromise = (promises, {description, value, id_item}) => {
		if (!(value instanceof File)) return;

		const dataToSubmit = {
			value,
			id_item,
			description,
			module : "user",
		};

		const promise = genericSerivices.add({ module : "files", dataToSubmit }).unwrap();

		promises.push(promise);
	};

	const handleSubmitFiles = async (files, id_item) => {
		try {
			const promises = [];

			for (const [key, value] of Object.entries(files)) {

				if (key === "other_files" && isValidArray(value)) {
					value.forEach( file => setFilePromise(promises, { description : "other_files", value : file, id_item}));
				}

				setFilePromise(promises, {  description : key, value, id_item});

			}
			const response = await Promise.allSettled(promises);

			const status = response.map( result => result.value.status).filter( status => status !== 200);

			return status;
		} catch (error) {
			console.error("[Error on handleSubmitFiles]: ", error);
			return error;
		}
	};

	const handleRemovedFiles = async () => {
		try {
			const promises = filesToRemoved.map(file => genericSerivices.remove({ module : `files/${file._id}` }).unwrap());

			const response = await Promise.allSettled(promises);

			const status = response.map( result => result.value.status).filter( status => status !== 200);

			fileActions.filesRemoved();

			return status;
		} catch (error) {
			console.error("[Error on handleRemovedFiles]: ", error);
			return error;
		}
	};

	const handleSuccessStatus = async (files, { data : userData }) => {
		try {
			if (isEditing) {
				const filesRemovedStatus = await handleRemovedFiles();

				if (filesRemovedStatus > 0) {
					toaster.error({
						title : "Error al intentar eliminar un archivo.",
						body  : "Ocurrió un problema al intentar eliminar un archivo.",
					});
				}
			}

			const fileStatus = await handleSubmitFiles(files, userData._id);

			setLoading(false);

			if (fileStatus > 0) {
				toaster.error({
					icon  : UserX,
					title : "Error al intentar subir un archivo.",
					body  : "Ocurrió un error al subir un archivo, favor de verificar el tipo de archivo.",
				});
				return;
			}

			toaster.success({
				icon  : UserPlus,
				title : "Usuario " + (isEditing ? "Editado" : "Creado"),
				body  : <p>El usuario <b>{userData.email}</b> se ha {(isEditing ? "editado" : "creado")} exitosamente!
					{!isEditing && <>Le enviamos un correo con sus accesos a <b>{userData.email}</b></>}</p>,
			});

			if (isUserDetails?._id) {
				genericSerivices.get({ module : `users/${isEditing}`, actionType : "getUser" });
			} else {
				genericSerivices.getAll({ module : "users", actionType : "getAllUsers" });
			}

			usersActions.closeCreateUserModal();
		} catch (error) {
			console.error("Error on handleSuccessStatus: ", error);
			setLoading(false);
		}

	};

	const handleConflictStatus = (userData, err) => {
		for (const [key, value] of Object.entries(err)) {
			if (!value) continue;

			toaster.warning({
				icon  : UserMinus,
				title : "Campo duplicado",
				body  : <p>El campo <b>{userData[key]}</b> ya está siendo utilizado por otro usuario,
					favor de ingresar un valor diferente</p>,
			});
			hookForm.setError(key, {
				type    : "manual",
				message : "Campo duplicado.",
			});
		}
	};

	const handleSubmitForm = (...args) => {

		if (!handleOpenExtraForm()) {
			unregister(["record.shf", "record.key_tdf", "record.key_perit_independent"]);
			hookForm.clearErrors([...Object.keys(sidebarControllerInpts)]);
		}

		handleValidateControllerInpts();

		hookForm.handleSubmit(async (intpsData) => {
			try {
				setLoading(true);

				const copyData = { ...intpsData };
				const files    = { ...intpsData.files };

				const role   = copyData.role.value;
				const places = copyData.places.map(place => place.value);

				delete copyData["role"];
				delete copyData["image"];
				delete copyData["files"];
				delete copyData["places"];

				let extraForm = {};

				if (handleOpenExtraForm()) {
					extraForm = { record : { ...copyData?.record, type_shf : copyData?.record?.type_shf?.value} };
				}

				delete copyData["record"];

				const dataToSubmit = {
					role,
					image,
					places,
					is_delete_image : !image ? true : false,

					...copyData,
					...extraForm,
				};

				let response;

				if (isEditing) {
					response = await genericSerivices.update({ module : `users/${userToEdit._id}`, dataToSubmit }).unwrap();
				} else {
					response = await genericSerivices.add({ module : "users", dataToSubmit }).unwrap();
				}

				const { status, data } = await response;

				if (status === 200) {
					handleSuccessStatus(files, data);
					return;
				}

				setLoading(false);

				if (status === 409) {
					const { err } = data;

					handleConflictStatus(copyData, err);
					return;
				}

				toaster.error({
					icon  : UserX,
					title : "Error",
					body  : "Ocurrió un error, favor de revisar su conexión a internet.",
				});

			} catch (error) {
				console.error("Error on handleSubmitForm: ", error);
				setLoading(false);
			}
		})(...args);
	};

	useEffect(() => {
		(isUserDetails?._id && isOpen) ? usersActions.setModalData({ data : { ...isUserDetails }}) :
			genericSerivices.get({ module : `users/${isEditing}`,  actionType : "setModalData" });

		reset({ ...defaultValues });
	}, [isOpen, isEditing]);

	useEffect(() => {
		if (userToEdit) reset({ ...defaultValues });
	}, [userToEdit]);

	return (
		<ModalUsers
			delegations={{
				ac,
				image,
				isOpen,
				loading,
				hookForm,
				setImage,
				isEditing,
				shfOptions,
				rolesOptions,
				statesOptions,
				handleSubmitForm,
				handleSelectChange,
				handleOpenExtraForm,
				...rest,
			}}
		/>
	);
};

const mapDispatchToState = ({ _usersSlice, _filesSlice, _authSlice }) => ({
	userToEdit     : _usersSlice?.modal?.info        ?? {},
	isUserDetails  : _usersSlice?.data               ?? {},
	isEditing      : _usersSlice?.modal?.userToEdit  ?? "",
	role           : _authSlice?.user?.role          ?? "",
	isOpen         : _usersSlice?.modal?.open        ?? false,
	filesToRemoved : _filesSlice?.filesToRemoved     ?? [],
});

const mapDispatchToProps = bindAll({

	UsersActions : UsersSlice.actions,
	FileActions  : FilesSlice.actions,
	GenericSerivices,
});

export default connect(mapDispatchToState, mapDispatchToProps)(ModalUsersContainer);
